import test from 'ava';
import axios from 'axios';
import {
    BASE_URL as url,
    ADAFRUIT_IO_KEY as ioKey,
    ADAFRUIT_IO_USERNAME as user,
    feed as defaultFeed
} from './fixtures';

    // curl -H "X-AIO-Key: b780002b85d6411ca0ad9f9c60195f72" \
    // https://io.adafruit.com/api/v2/test_username/feeds

export const getApi = async () => {
    const client = axios.create({
        baseURL: url + user,
        validateStatus() { return true; },
    });

    return client;
};

export const auth = api => {
    api.defaults.headers['X-AIO-Key'] = ioKey;
};

export const logout = api => {
    delete api.defaults.headers['X-AIO-Key'];
};

export const responseErrMessage = response => {
    return JSON.stringify(response.data, null, 2);
};

export const createFeed = async (t, feedObject = defaultFeed ) => {
    const resp  = await t.context.api.post('/feeds', feedObject);
    console.log(resp);

    t.is(resp.status, 201, responseErrMessage(resp));

    return resp.data
};